import { makeAutoObservable } from "mobx"

export default class StuffStore {
    constructor() {
        this._stuffs = [
        ]
        this._types = [
            {id: 1, name: ''},
            {id: 2, name: ''},
            {id: 3, name: ''},
            {id: 4, name: ''}
        ]
        this._genre = [
            {id: 1, name: ''},
            {id: 2, name: ''},
            {id: 3, name: ''},
            {id: 4, name: ''},
            {id: 5, name: ''},
            {id: 6, name: ''},
        ]
        this._category = [
            {id: 1, name: '  '},
            {id: 2, name: ''},
            {id: 3, name: ''},
            {id: 4, name: ' '},
            {id: 5, name: ' '}
        ]
        this._author = [
            {id: 1, name: '..'},
            {id: 2, name: 'dsfsefesfdf'},
            {id: 3, name: 'landes'},
            {id: 4, name: 'carnie'},
            {id: 5, name: 'ganses'},
            {id: 6, name: 'ij9j=]k[][lp'},
        ]
        this._posts = []
        this._selectedType = {}
        this._selectedAuthor = {}
        this._selectedGenre = {}
        this._selectedCategory = {}
        this._page = 1
        this._totalCount = 0
        this._limit = 12
        makeAutoObservable(this)
    }

    
    setPosts(posts) {
        this._posts = posts
    }
    setIsStuffs(stuffs) {
        this._stuffs = stuffs
    }
    setType(types) {
        this._types = types
    }
    setGenre(genre) {
        this._genre = genre
    }
    setAuthor(author) {
        this._author = author
    }
    setCategory(category) {
        this._category = category
    }
    setSelectedType(type) {
        this.setPage(1)
        this._selectedType = type
    }
    setSelectedAuthor(author) {
        this.setPage(1)
        this._selectedAuthor = author
    }
    setPage(page) {
        this._page = page
    }
    setTotalCount(count) {
        this._totalCount = count
    }

    get IsStuffs() {
        return this._stuffs
    }
    get posts() {
        return this._posts 
    }
    get types() {
        return this._types
    }
    get genre() {
        return this._genre
    }
    get author() {
        return this._author
    }
    get category() {
        return this._category
    }
    get selectedType() {
        return this._selectedType
    }
    get selectedAuthor() {
        return this._selectedAuthor
    }
    get totalCount() {
        return this._totalCount
    }
    get page() {
        return this._page
    }
    get limit() {
        return this._limit
    }
}
